#modal {
  height: 100%;
  opacity: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, .8);
  padding: 2.4rem;
  transition: -webkit-transform .2s ease-in, transform .2s ease-in, opacity .15s linear;
  position: fixed;
  top: 0;
  bottom: 0;
  -webkit-transform: translate3d(-101vw, 0, 0);
  transform: translate3d(-101vw, 0, 0);
}

#modal.is-active:target {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

#modal.is-active:target #modal-image {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

#modal-inner {
  height: 100%;
  width: 100%;
  padding: 2.4rem;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  overflow: scroll;
}

@media screen and (min-width: 640px) {
  #modal-inner {
    padding: 4.8rem;
  }
}

#modal-image {
  height: auto;
  max-width: 100%;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  transition: -webkit-transform .1s cubic-bezier(.2, 1.6, .55, 1.6) .19s, transform .1s cubic-bezier(.2, 1.6, .55, 1.6) .19s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
  display: block;
  -webkit-transform: translate3d(-4.8rem, 0, 0);
  transform: translate3d(-4.8rem, 0, 0);
}

#modal-image.is-loading {
  -webkit-animation-name: fade-in-out;
  animation-name: fade-in-out;
}

